ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
}

.gallery-section {
  padding: 60px 0;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: -30px;
  display: flex;
}

.gallery__item {
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
}

.gallery-img {
  width: 100%;
  height: 309px;
  max-width: unset;
  object-fit: cover;
}

.backdrop {
  width: 100vw;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, .725);
  background-size: cover;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  display: none;
}

@keyframes spinner-line-fade-more {
  0%, 100% {
    opacity: 0;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-quick {
  0%, 39%, 100% {
    opacity: .25;
  }

  40% {
    opacity: 1;
  }
}

@keyframes spinner-line-fade-default {
  0%, 100% {
    opacity: .22;
  }

  1% {
    opacity: 1;
  }
}

@keyframes spinner-line-shrink {
  0%, 25%, 100% {
    opacity: .25;
    transform: scale(.5);
  }

  26% {
    opacity: 1;
    transform: scale(1);
  }
}

